.new-post-page {

        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
    
        form {
    
            .add-new-post-heading {
                font-size: 1.2rem;
                color: var(--pink);
                text-align: center;
                margin-bottom: 20px;
    
                @media screen and (max-width: 450px) {
                    margin-top: 220px;
                }
                @media screen and (max-width: 325px) {
                    margin-top: 90px;
                }
            }
    
            width: 40%;
            
            ul {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 0.5rem;
                width: 100%;
    
            li {
                width: 100%;
                input, textarea {
                    padding: 0.3rem;
                    border-radius: 8px;
                    padding-left: 0.5rem;
                    width: 100%;
                }
    
                input {
                    margin-bottom: 10px;
                }
    
                textarea {
                    height: 150px;
                    @media screen and (max-width: 450px) {
                        height: 250px;
                    }
        
                }
              
                
            }
    
            .btns-add-new-post {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: row;
                gap: 1rem;
    
                 button {
                    font-size: 1rem;
                    margin: 5px;
                    color: var(--darkpurple);
                    border: 2px solid var(--lightpurple);
                    padding: 2px 5px;
                    border-radius: 8px;
                    transition: 0.4s ease-in-out;
    
                    &:hover {
                        background-color: var(--lightpurple);
                        color: white;
                    }
                }
    
                @media screen and (max-width: 450px) {
                    flex-direction: column;
                    gap: 0.5rem;
                    margin-bottom: 12rem;
                }
    
            }
          }
    
          @media screen and (max-width: 800px) {
            width: 80%;
        }
          @media screen and (max-width: 450px) {
            width: 80%;
        }
        }
    
    

}